@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe_recipe__3ZMKD {
  width: 100%;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.884);
  padding: 15px 25px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 5px 5px 20px rgb(71, 71, 71);
  color: rgb(71, 71, 71);
}

.recipe_recipe__3ZMKD img {
  width: 100%;
  height: 300px;
  border-radius: 4px;
}

h1 {
  margin: 20px 10px;
}

ol {
  margin: 0 0 20px 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  min-height: 100vh;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

html {
  font-family: "Nunito", sans-serif;
  font-size: 12px;
  font-weight: 600;
}

section {
  min-height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-image: linear-gradient(180deg, #a9c9ff 0%, #ffbbec 100%);
}

.container {
  width: 90%;
  height: auto;
  max-width: 0 auto;
}

.brand {
  text-align: center;
  font-size: 4rem;
  color: whitesmoke;
  text-shadow: 2px 2px 5px #8b8b8b;
  margin-bottom: 30px;
}

.search-form {
  width: 90%;
  max-width: 400px;
  margin: 20px auto;
  background-color: rgb(223, 223, 223);
  border-radius: 4px;
  display: -webkit-flex;
  display: flex;
}

.search-bar {
  width: 90%;
  background-color: transparent;
  padding: 10px;
  border: none;
  outline: none;
  font-size: 2rem;
  display: inline-block;
}

.search-form button {
  display: inline-block;
  margin: 0 0 0 5px;
  padding: 10px;
  font-size: 16px;
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  background-color: lightslategray;
  color: whitesmoke;
}

.recipes {
  margin-top: 50px;
  width: 100%;
  display: grid;
  grid-gap: 25px;
  grid-template: auto / repeat(auto-fit, minmax(300px, 1fr));
}

